@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&display=swap');

body {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Nunito', sans-serif;
  background-color: #ac598d;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.App {
  width: 500px;
  border-radius: 30px;
  padding: 40px;
  background-color: #fff;
  position: relative;
}
.result {
  text-align: center;
}
button{
  font-weight: bold;
  font-family: 'Nunito', sans-serif;
  border-radius: 30px;
  border: 0;
  padding: 15px 30px;
  cursor: pointer;
  margin-top: 20px;
}
button:hover{
  background-color: pink;
}
.progress {
  height: 10px;
  border-radius: 30px;
  background-color: rgb(232, 232, 232);
  margin-bottom: 25px;
}
.progress__inner {
    height: 100%;
    border-radius: 30px;
    width: 80%;
    background: rgb(18, 231, 48);
    transition: all 0.3s ease-in-out;
    background: linear-gradient(90deg, rgba(18, 231, 48, 1) 0%, rgba(0, 212, 255, 1) 100%);
  }


img{
  width: 100px;
}

ul{
  list-style: none;
  padding: 0;
}
li{
  padding: 15px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  margin-bottom: 10px;
  cursor: pointer;
}
li:hover{
  border: 2px solid rgba(0, 0, 0, 0.3);
}
